  <!-- </teleport> -->
  <SharingComponent />
</template>

<script>
import { ref, onMounted } from "vue";
import gsap from "gsap";
import SceneNo from "@/Scene/";
import HomeView from "@/views/HomeView";
import SharingComponent from "@/components/Sharing";
import settings from "@/Scene/config";
export default {
  // mixins: [Resize],
  components: {
    SceneNo,
    HomeView,
    SharingComponent,
  },
  setup() {
    const scene = ref(null);
    const mastHead = ref(null);
    const scrollProxy = ref(null);
    const tl = new gsap.timeline({
      paused: true,
    });

    onMounted(() => {
      settings.scene.updateScene(0);

      tl
        .add(mastHead.value.leaveTL, "a")
        .add(scene.value.section1, "a")
        .add(scene.value.section2, "b");

      const maxH =
        scrollProxy.value.scrollHeight - scrollProxy.value.clientHeight;

      scrollProxy.value.addEventListener("scroll", () => {
        const progress = scrollProxy.value.scrollTop / maxH;
        const attrs = {
          ease: "linear",
        };
        const timeShift =
          Math.abs(progress * tl.duration() - tl.time()) > 2.0;
        if (timeShift) {
          attrs.duration = 1.0;
        }
        tl.tweenTo(progress * tl.duration(), attrs);
      });
    });
    return {
      scene,
      mastHead,
      scrollProxy,
    };
  },
};
</script>

