import {
  Vector2,
  Vector3,
  WebGLRenderer,
  Scene,
  sRGBEncoding,
  ReinhardToneMapping,
  Color,
  Object3D
} from "three";

import Device from "@/pure/Device";
import Camera from "./cameras/FreeCamera";

import PostProcessing from "@/Scene/postprocessing/index";
// import Skeleton from "./Skeleton/index.js";
import Galaxy from "./Galaxy/index.js";
import Atlas from "./Atlas/index.js";
import Glyph from "./Glyph/index.js";
import Overlay from "./Overlay/index.js";
// import UI from "./UI.js";

import gsap from "gsap";

import { getFovHeigth } from "@trinketmage/sword";
import settings from "./config";
// import SETTINGS from "./SETTINGS";

// var _ray = new THREE.Raycaster();

export default class MainScene {
  constructor({ $el, initScene = -1 }) {
    settings.mouse = new Vector2(0, 0);
    // settings.mouse3d = _ray.origin;
    settings.idx = initScene;

    settings.sizes = {
      scrollY: document.documentElement.scrollTop || document.body.scrollTop,
      viewport: {
        width: window.innerWidth,
        height: window.innerHeight
      },
      frustum: {
        height: 0,
        width: 0
      }
    };
    this.components = {};

    var canvas = $el;

    this.targetMouse = new Vector2(0, 0);
    this.renderer = new WebGLRenderer({
      canvas: canvas,
      alpha: false,
      stencil: false,
      depth: true,
      powerPreference: "high-performance",
      antialias: false
    });

    this.renderer.physicallyCorrectLights = true;
    this.renderer.outputEncoding = sRGBEncoding;
    this.renderer.toneMapping = ReinhardToneMapping;

    this.renderer.setPixelRatio(Device.pixelRatio);
    
    this.renderer.autoClear = false;
    this.renderer.hasMoved = false;

    this.scene = new Scene();
    this.scene.background = new Color(0x10100f * 0.625);
    // this.scene.background = new Color(0x15304d).convertSRGBToLinear();

    this.cameras = [new Camera({ sizes: {
      width: this.renderer.domElement.parentElement.offsetWidth,
      height: this.renderer.domElement.parentElement.offsetHeight
    } })];
    this.camera = this.cameras[0].instance;
    this.camera.position.copy(
      initScene === -1
        ? settings.camera.initFrame.position.clone()
        : settings.camera.frames[initScene].position.clone()
    );

    this.camera.rotation.x =
      initScene === -1
        ? settings.camera.initFrame.rotation.x
        : settings.camera.frames[initScene].rotation.x;
    this.camera.rotation.y =
      initScene === -1
        ? settings.camera.initFrame.rotation.y
        : settings.camera.frames[initScene].rotation.y;
    this.camera.rotation.z =
      initScene === -1
        ? settings.camera.initFrame.rotation.z
        : settings.camera.frames[initScene].rotation.z;
        
    // this.camera.position.copy(settings.camera.frames[0].position.clone());
    // this.camera.rotation.copy(settings.camera.frames[0].rotation.clone());

    this.scene.add(this.cameras[0].container);
    this.main = new Object3D();
    this.scene.add(this.main);

    this.postprocessing = new PostProcessing({
      renderer: this.renderer,
      scene: this.scene,
      camera: this.camera
    });

    this.init();

    this.handleResize();
    this.x = this.handleResize.bind(this);
    window.addEventListener("resize", this.x, false);

    this.y = this.handleMousemove.bind(this);
    window.addEventListener("mousemove", this.y, { passive: true });

    gsap.ticker.add(this.render.bind(this));

    settings.scene = this; 
  }
  init() {
    this.components.galaxy = new Galaxy({
      scene: this.main,
      renderer: this.renderer,
      camera: this.camera
    });
    this.components.atlas = new Atlas({
      scene: this.main,
      renderer: this.renderer,
      camera: this.camera
    });
    this.components.overlay = new Overlay({
      scene: this.main,
    });

    this.glyph = new Glyph({scene: this.scene, fontSize: 1000});
    
    this.camera.layers.disable(1);
  }
  updateScene(idx = 0) {
    // if(idx === 0) {
    //   this.camera.layers.enable(0);
    // }
    this.renderer.renderLists.dispose();
    if (this.tween) this.tween.kill();
    const progress = { x: 0 };
    const pos = this.camera.position.clone();
    const rot = new Vector3().setFromEuler(this.camera.rotation);
    let frame =
      idx === -1 ? settings.camera.initFrame : settings.camera.frames[idx];
    const position = frame.position.clone();
    const rotation = frame.rotation.clone();
    frame.light.clone();
    // const newPos = pos.lerp(position, progress.x);
    this.tween = gsap.to(progress, 2.4 * settings.progress, {
      x: 1.0,
      ease: "power1.out",
      onUpdate: () => {
        this.camera.position.lerpVectors(pos, position, progress.x);
        this.camera.rotation.setFromVector3(
          new Vector3().lerpVectors(rot, rotation, progress.x)
        );
        settings.progress = progress.x;
      },
      onComplete: () => {
        if(idx !== 0) {
          this.camera.layers.disable(0);
        }
      }
    });
    settings.idx = idx;
  }
  handleMousemove(e) {
    settings.mouse.x = (e.pageX / settings.sizes.viewport.width) * 2 - 1;
    settings.mouse.y = -(e.pageY / settings.sizes.viewport.height) * 2 + 1;
    this.targetMouse.x = 0.05 * settings.mouse.x;
    this.targetMouse.y = 0.005 * settings.mouse.y;
    this.renderer.hasMoved = true;

    // if (settings.idx === 0 && settings.intersected) {
    //   this.skeleton.setRose(settings.intersected);
    // }
  }
  render(t) {
    this.main.rotation.y +=
      (-this.targetMouse.x - this.main.rotation.y) * 0.035;

    Object.keys(this.components).forEach(_ => {
      this.components[_].render(t, this.sizes);
    });
    // this.cameras[0].render();
    this.postprocessing.render(t);
    // this.renderer.render(this.scene, this.camera);
  }
  handleResize() {
    settings.sizes.viewport.width = this.renderer.domElement.parentElement.offsetWidth;
    settings.sizes.viewport.height = this.renderer.domElement.parentElement.offsetHeight;
    settings.sizes.viewport.halfWidth = settings.sizes.viewport.width * 0.5;
    settings.sizes.viewport.halfHeight = settings.sizes.viewport.height * 0.5;
    this.camera.aspect =
      settings.sizes.viewport.width / settings.sizes.viewport.height;
    this.camera.updateProjectionMatrix();
    settings.sizes.frustum.height = getFovHeigth(
      this.camera,
      3.125
    );
    
    settings.sizes.frustum.width =
      settings.sizes.frustum.height * this.camera.aspect;
    this.renderer.setSize(
      settings.sizes.viewport.width,
      settings.sizes.viewport.height
    );
    Object.keys(this.components).forEach(_ => {
      this.components[_].resize(this.sizes);
    });
    
    this.postprocessing.handleResize(settings.sizes);
    // console.log(this.sizes);
    if(this.glyph) this.glyph.handleResize(settings.sizes, this.camera);
  }
  destroy() {
    window.removeEventListener("resize", this.x);
    document.removeEventListener("mousemove", this.y);
    this.renderer.dispose();
    this.postprocessing.dispose();
    // this.freeCamera.dispose();
    if (this.cameras[0]) this.cameras[0].dispose();
    if (this.skeleton) this.skeleton.dispose();
  }
}
