import {
  Vector2,
  // Vector3,
  Mesh,
  Object3D,
  Uniform,
  Color,
  RawShaderMaterial,
  TextureLoader,
  // PlaneBufferGeometry,
  DoubleSide
} from "three";
import vertexShader from "@/Scene/Glyph/shaders/vertex.glsl";
import fragmentShader from "@/Scene/Glyph/shaders/fragment.glsl";
import createGeometry from "@/pure/glFont";

import font from "@/assets/fonts/Assistant-Regular.json";

export default class Glyph {
  constructor({
    scene,
    caption = "HI, MY NAME IS REMI.\nI HELP CREATIVES & THINKERS\nFROM AROUND THE WORLD\nTO DESIGN AND BUILD\nMEANINGFUL AND MEMORABLE\nEXPERIENCES.",
    fontSize = font.info.size
  }) {
    // this.material = material;
    const textureLoader = new TextureLoader();
    const texture = new Uniform(textureLoader.load( "/assets/fonts/Assistant-regular.png" ));
    this.scene = scene;
    this.fontSize = fontSize;
    this.caption = caption;

    // this.scale = 0.002125;
    this.scale = 1.0;
    
    this.mdsf = {
      transparent: true,
      uniforms: {
        opacity: { type: "f", value: 0.0},
        time: { type: "f", value: 0 },
        map: texture,
        alphaMap: new Uniform(textureLoader.load( "/assets/transition4.png" )),
        color: { type: "c", value: new Color(0xece9e3) },
        resolution: { type: "c", value: new Vector2(0, 0) },
        total: { type: "f", value: this.caption.length }
        // mouse: { type: "c", value: defaultPos },
        // end: { type: "c", value: new Color(0xff0000) }
      },
      side: DoubleSide,
      vertexShader,
      fragmentShader,
      depthTest: false
    };
    this.material = new RawShaderMaterial(this.mdsf);
    this.geometry = new createGeometry({
      text: caption,
      font,
      align: "left",
      width: 1000,
      // flipY: 1,
      letterSpacing: 13.125,
      lineHeight: 64
    });
    // console.log(this.geometry);
    // this.geometry = new PlaneBufferGeometry(1 ,1 ,1);

    
    this.layout = this.geometry.layout;
    this.text = new Mesh(this.geometry, this.material);
    this.text.renderOrder = 2;
    // this.text.geometry.computeBoundingBox();


    this.text.matrixAutoUpdate = false;
    this.group = new Object3D();
    this.group.matrixAutoUpdate = false;
    this.textAnchor = new Object3D();
    this.textAnchor.matrixAutoUpdate = false;
    
    this.textAnchor.add(this.text);
    this.text.layers.set(1);

    // var center = new Vector3();
    // this.text.geometry.computeBoundingBox();
    // this.text.geometry.boundingBox.getCenter(center);
    // this.textAnchor.position.x = -center.x;
    // this.textAnchor.position.y = center.y;
    // this.textAnchor.updateMatrix();


    // this.text.rotation.x = Math.PI + 0.075 * this.scale;
    // this.text.updateMatrix();
    // this.group.scale.x = this.scale;
    // this.group.scale.y = this.scale;
    // this.group.updateMatrix();
    // this.textAnchor.position.x = -1000;
    // this.textAnchor.position.y = -200;
    // this.textAnchor.updateMatrix();
    
      
    this.group.add(this.textAnchor);
    this.scene.add(this.group);

    // this.setBindings();
  }
  setBindings() {
    window.addEventListener('keydown', event => {
      if (event.defaultPrevented) {
        return;
      }
      if (!event.metaKey) {
        if (event.which <= 90 && event.which >= 48) {
          this.caption = this.caption + event.key;
          this.geometry.update(this.caption);
          event.preventDefault();
        } else {
          console.log("nonchar", event)
        }
      }
    }, true);
  }
  render() {
    // this.material.uniforms.time.value = t;
    // this.material.uniforms.mouse.value = pos;
    // this.text.rotation.y = 12;
    // console.log(this.text.rotation);
  }
  handleResize(sizes) {
    this.text.geometry.computeBoundingBox();
    
    const texel = sizes.frustum.width / sizes.viewport.width * 0.5;
    this.scale = texel;

    this.text.scale.x = this.scale;
    this.text.scale.y = this.scale;
    this.text.rotation.x = Math.PI;
    this.text.updateMatrix();

    const margin = texel * 80;

    this.group.position.x = -(sizes.frustum.width * 0.5) + margin;
    this.group.rotation.x = 0.075;
    const oy = Math.abs(this.text.geometry.boundingBox.min.y) * this.scale;
    const y = sizes.frustum.height * 0.5 - oy - margin;
    // const y = -sizes.frustum.height * 0.5 + this.scale + texel * 80;

    const ry = 0.075 * 3.125;
    
    this.group.position.y = ry + -0.9050000000000006 + 1.3125 + y;

    this.group.updateMatrix();

  }
  dispose() {
    this.scene.remove(this.textAnchor);
    this.geometry.dispose();
    this.material.dispose();
  }
}
