<template>
  <div class="mast-head">
    <h1>
      <span class="title-caption" ref="title">RÉMI TRAN</span>
    </h1>
    <span class="baseline" ref="baseline">
      <span class="gold">CODER</span> / <span class="pink">CARTOGRAPHER</span>
    </span>
    <!-- <span class="baseline" ref="baseline">
      DEVELOPER
    </span> -->
  </div>
</template>

<script>
import gsap from "gsap"
import SplitText from "@/pure/gsap/SplitText"
export default {
  mounted() {
    this.title = new SplitText(this.$refs.title, {type:"words,chars"})
    this.baseline = new SplitText(this.$refs.baseline, {type:"words,chars"})
    this.tl = new gsap.timeline()

    this.tl
      .from(this.title.chars, {
        duration: .5, 
        stagger: 0.1015625,
        alpha: 0, 
        ease: 'none'
      }, 'a')
      .from(this.title.chars, {
        duration: 1.5, 
        yPercent: -110, 
        force3D: false,
        stagger: 0.1015625,
        ease: 'quint.out'
      }, 'a')
      .from(this.baseline.chars, {
        duration: 1.5, 
        yPercent: 100, 
        stagger: 0.0625,
        force3D: false,
        ease: 'quint.out'
      }, 'a')

      this.leaveTL = new gsap.timeline({
        onStart: () => {
          this.tl.progress(1);
        }
      });
      this.leaveTL
        .to(this.title.chars, {
          duration: .625, 
          // stagger: 0.1015625,

          stagger: {
            each: 0.1015625,
            from: "end"
          },
          autoAlpha: 0, 
          ease: 'none'
        }, 'a')
        .to(this.title.chars, {
          duration: 1.5, 
          yPercent: -110, 
          force3D: false,
          stagger: {
            each: 0.1015625,
            from: "end"
          },
          ease: 'quint.inOut'
        }, 'a')
        .to(this.baseline.chars, {
          duration: 1.5, 
          yPercent: 100, 
          // stagger: 0.0625,
          stagger: {
            each: 0.0625,
            from: "end"
          },
          force3D: false,
          ease: 'quint.inOut'
        }, 'a')
  },
  methods: {
    // handleLeave() {
    //   // console.log();
    //   return gsap


    // },
    handleRender() {
      // console.log();
      this.momentum.handleRender()


    },
    handleWheel(x, y, eve) {
      this.momentum.addTransformableMomentum(x, y, eve, (willScroll) => {
        if (willScroll) {
          eve.preventDefault();
        }
      });
    }
  }
}
</script>

