import {
  SphereGeometry,
  Mesh,
  MeshStandardMaterial as MeshPhysicalMaterial,
  TextureLoader,
  Color,
  DirectionalLight,
  CubeTextureLoader,
  sRGBEncoding,
  // SpriteMaterial,
  PlaneGeometry,
  RawShaderMaterial,
  Uniform,
  // Sprite,
} from "three";
import AlphaFragment from "./alpha.fragment.glsl";
import ZodiacVertex from "./zodiac.vertex.glsl";
import ZodiacFragment from "./zodiac.fragment.glsl";

import SETTINGS from "./../SETTINGS";
// import Tweakpane from "tweakpane";

export default class {
  constructor({ scene, camera, renderer }) {
    this.scene = scene;
    this.renderer = renderer;
    this.camera = camera;
    this.geometry = new SphereGeometry(1, 64, 64);
    var directionalLight = new DirectionalLight( 0xffffff, 5.0 );
    directionalLight.position.set( 0, 10, 20 );
    directionalLight.layers.set(1);
    this.scene.add( directionalLight );

    this.init();
    const textureLoader = new TextureLoader();
    const worldMap = textureLoader.load( "/assets/map.png" );
    const alphaMap = textureLoader.load( "/assets/transition4.png" );
    worldMap.encoding = sRGBEncoding;

    // const pane = new Tweakpane();
    // pane.element.parentNode.style.zIndex = 1000;

    // const rendering = pane.addFolder({ title: 'Rendering' });
    // rendering.addInput(SETTINGS, 'z', { min: 0, max: 1, step: 0.001 });
      
    this.material = new MeshPhysicalMaterial({
      color: new Color(0xa58946).convertSRGBToLinear(),
      metalness: 1.0,
      roughness: SETTINGS.atlas.roughness,
      envMap: this.textureCube,
      // refractionRatio: 1.0,
      // envMapIntensity: 1,
      emissive: new Color(0xa58946),
      emissiveIntensity: 0.0625,
      // roughnessMap: worldMap,
      map: worldMap,
      alphaMap,
      transparent: true,
      // opacity: 0.5
      // clearcoatRoughness: 1.0
      userData: {
        transition: { value: SETTINGS.atlas.transition }
      }
    }); 

    this.material.onBeforeCompile = shader => {
      shader.uniforms.transition = this.material.userData.transition //pass this input by reference
   
      shader.fragmentShader = 'uniform float transition;\n' + shader.fragmentShader
      shader.fragmentShader =
        shader.fragmentShader.replace(
          '#include <alphamap_fragment>',
          AlphaFragment
        )
    };
    this.sphere = new Mesh( this.geometry, this.material );
    this.sphere.layers.set(1);
    this.sphere.matrixAutoUpdate = false;
    this.sphere.position.y = 0.65;
    this.sphere.rotation.y = -1.3125;
    this.sphere.rotation.x = .875;
    this.sphere.scale.set(.9, .9, .9);
    
    // this.sphere.layers.set(2);
    // this.sphere.position.z = 1.152
    this.sphere.updateMatrix();
    scene.add( this.sphere );

    const zodiac = textureLoader.load( '/assets/zodiac.png' );
    zodiac.encoding = sRGBEncoding;
    // const material = new SpriteMaterial( { map: zodiac, opacity: 1 } );
    const material = new RawShaderMaterial({
      transparent: true,
      // depthTest: false,
      uniforms: {
        // uColor: {
        //   type: "v3",
        //   value: new Color(0x000000)
        // },
        uAlpha: new Uniform(0),
        map: new Uniform(zodiac),
        alphaMap: new Uniform(alphaMap),
      },
      vertexShader: ZodiacVertex,
      fragmentShader: ZodiacFragment
    });
    // console.log("material", material);
    var geometry = new PlaneGeometry(1, 1, 1);
    this.zodiac = new Mesh(geometry, material );
    // console.log(sprite.modelViewMatrix.elements);
    
    // this.zodiac.modelViewMatrix.elements[0] = 1.0;
    // this.zodiac.modelViewMatrix.elements[1] = 0.0;
    // this.zodiac.modelViewMatrix.elements[2] = 0.0;

    // this.zodiac.modelViewMatrix.elements[4] = 0.0;
    // this.zodiac.modelViewMatrix.elements[5] = 1.0;
    // this.zodiac.modelViewMatrix.elements[6] = 0.0;

    // this.zodiac.modelViewMatrix.elements[8] = 0.0;
    // this.zodiac.modelViewMatrix.elements[0] = 0.0;
    // this.zodiac.modelViewMatrix.elements[10] = 1.0;
    
    const scale = 2.95 * .9;
    this.zodiac.scale.set(scale, scale, scale);
    // sprite.visible = false;
    this.sphere.add( this.zodiac );
  }
  init() {

    var r = '/assets/cubemap/';
    var urls = [ r + 'px.png', r + 'nx.png', r + 'py.png', r + 'ny.png', r + 'pz.png', r + 'nz.png' ];
    this.textureCube = new CubeTextureLoader().load( urls );
    // this.textureCube.encoding = sRGBEncoding;
  }
  render() {
  }
  dispose() {
  }
  resize() {}
}
