import {
  Mesh,
  BufferGeometry,
  RawShaderMaterial,
  Vector3,
  BufferAttribute,
  Uniform,
  Color,
  TextureLoader,
} from "three";
import vertexShader from "./vertex.glsl";
import fragmentShader from "./fragment.glsl";

import settings from "./../config";

export default class {
  constructor({ scene }) {
    const { width, height } = settings.sizes.viewport;
    const textureLoader = new TextureLoader();
    const alphaMap = textureLoader.load( "/assets/transition4.png" );
    this.material = new RawShaderMaterial({
      uniforms: {
        tDiffuse: {
          value: null
        },
        resolution: {
          value: new Vector3(width, height, width / height)
        },
        color: new Uniform(new Color(0xc1bbb6)),
        alphaMap: new Uniform(alphaMap),
        uAlpha: new Uniform(0),
        uLine: new Uniform(0),
      },
      vertexShader,
      fragmentShader,
      transparent: true,
    });
    const geometry = new BufferGeometry();

    const vertices = new Float32Array([-1, 3, 0, -1, -1, 0, 3, -1, 0]);
    geometry.setAttribute('position', new BufferAttribute(vertices, 3));
    const uv = new Float32Array([0, 2, 0, 0, 2, 0]);
    geometry.setAttribute("uv", new BufferAttribute(uv, 2));

    this.mesh = new Mesh(geometry, this.material);
    this.mesh.renderOrder = 1;
    // this.mesh.visible = false;

    scene.add(this.mesh);
  }
  resize() {
    const { width, height } = settings.sizes.viewport;
    this.material.uniforms.resolution.value.set(width, height, width / height);
  }
  render() {}
}