import {
  BufferGeometry,
  Float32BufferAttribute,
  Points,
  RawShaderMaterial,
  Color,
  PlaneGeometry,
  InstancedBufferGeometry,
  MeshBasicMaterial,
  InstancedMesh,
  AdditiveBlending,
  MathUtils as THREEMath,
  // Object3D,
  // FogExp2,
  Mesh,
  Raycaster,
  Uniform,
  RepeatWrapping,
  InstancedBufferAttribute,
  Quaternion,
  Vector2,
  Vector3,
  DoubleSide,
  Group
} from "three";

import VertexShader from "./cloud.vertex.glsl";
import FragmentShader from "./cloud.fragment.glsl";

import GoldVertexShader from "./gold.vertex.glsl";
import GoldFragmentShader from "./gold.fragment.glsl";

import positionFragmentShader from "./position.fragment.glsl";

import settings from "./../config";

import { GPUComputationRenderer } from "three/examples/jsm/misc/GPUComputationRenderer.js";

import SETTINGS from "./../SETTINGS";

function gaussianRandom() {
  var r1, r2, w, x, y;
  do {
    r1 = 2 * Math.random() - 1;
    r2 = 2 * Math.random() - 1;
    w = r1 * r1 + r2 * r2;
  } while (w >= 1);

  w = Math.sqrt((-2 * Math.log(w)) / w);

  x = r1 * w;
  y = r2 * w;

  return {
    x,
    y
  };
}

// var dummy = new Object3D();
var upVector = new Vector3(0,1,0); 
var farVector = new Vector3(1,0,0); 
var quaternion = new Quaternion();
quaternion.setFromAxisAngle( upVector, 0.0 );

export default class {
  constructor({ scene, camera, renderer }) {
    this.scene = scene;
    this.renderer = renderer;
    // this.scene.fog = new FogExp2(new Color(0x10100f * 0.625), 0.1825);
    this.camera = camera;
    this.currentIdx = 0;
    this.idx = 0;
    this.group = new Group();
    this.scene.add(this.group);
    this.initStars(settings.sizes);
    this.initGoldLeaves(settings.sizes);
  }
  initStars() {
    var particles = 512;
    var geometry = new BufferGeometry();
    var positions = [];
    var colors = [];
    var alphas = [];
    var aSizes = [];
    var indexes = [];
    var n = 8.805374471080656 * 2.0,
      n2 = n / 2,
      spread = 0.0825;
    const colorList = [
      new Color(0xdaa520),
      new Color(0xd4af37),
      new Color(0x6f3a31)
    ];
    for (var i = 0; i < particles; i++) {
      var x = Math.random() * n * spread * 4 - n2 * spread * 4;
      var y = 1.625 + (Math.random() - 0.5) * (0.3125 + Math.abs(Math.sin(x)));
      var z = Math.random() * n * spread - spread;
      positions.push(x, y, z);
      const rand = THREEMath.randInt(0, colorList.length - 1);
      var s = 10 + Math.pow(Math.random() * 1.875, 2 * (rand + 1.975));
      aSizes.push(s);
      // const c = new Color(0xd4af37);
      const c = colorList[rand];
      colors.push(c.r, c.g, c.b);
      alphas.push(Math.pow(Math.random(), 3) * rand);
      indexes.push(i);
    }
    geometry.setAttribute("position", new Float32BufferAttribute(positions, 3));
    geometry.setAttribute("color", new Float32BufferAttribute(colors, 3));
    geometry.setAttribute("size", new Float32BufferAttribute(aSizes, 1));
    geometry.setAttribute("pindex", new Float32BufferAttribute(indexes, 1));
    geometry.setAttribute("alpha", new Float32BufferAttribute(alphas, 1));
    var material = new RawShaderMaterial({
      transparent: true,
      // depthTest: false,
      uniforms: {
        uColor: {
          type: "v3",
          value: new Color(0xffffff)
        },
        uAlpha: {
          value: 1.0
        },
        time: {
          value: 1.0
        }
      },
      blending: AdditiveBlending,
      vertexShader: VertexShader,
      fragmentShader: FragmentShader
    });
    this.stars = new Points(geometry, material);
    this.stars.matrixAutoUpdate = false;
    this.stars.layers.set(0);
    this.group.add(this.stars);
  }
  initGoldLeaves() {
    this.width = 128;
    this.initComputeRenderer();
    this.initRay();

    var amount = Math.pow(this.width, 2.0);

    var planeGeometry = new PlaneGeometry(0.0125, 0.0125, 1);
    planeGeometry.rotateX(-Math.PI * 0.5);

    var references = [];
    var scales = [];
    var instanceQuaternions = [];
    var instanceQuaternionsDefs = [];

    for (var i2 = 0; i2 < amount; i2++) {
      var x = (i2 % this.width) / this.width;
      var y = ~~(i2 / this.width) / this.width;
      references.push(x, y);
      scales.push(Math.random() * 0.5 + 0.5);

      var ry = (Math.random() - 0.5) * Math.PI * 2;
      quaternion.setFromAxisAngle( upVector, ry );
      quaternion.normalize();	
      instanceQuaternions.push( quaternion.x, quaternion.y, quaternion.z, quaternion.w );

      var rz = (Math.random() - 0.5) * Math.PI * 2;
      quaternion.setFromAxisAngle( upVector, ry );
      quaternion.setFromAxisAngle( farVector, rz );
      quaternion.normalize();	
      instanceQuaternionsDefs.push( quaternion.x, quaternion.y, quaternion.z, quaternion.w );
    }
    var geometry = new InstancedBufferGeometry();
    geometry.index = planeGeometry.index;
    geometry.attributes = planeGeometry.attributes;

    geometry.setAttribute(
      "reference",
      new InstancedBufferAttribute(
        new Float32Array(references), 2, false
      )
    );
    geometry.setAttribute(
      "scale",
      new InstancedBufferAttribute(
        new Float32Array(scales), 1, false
      )
    );
    geometry.setAttribute(
      "quaternion",
      new InstancedBufferAttribute(
        new Float32Array(instanceQuaternions), 4, false
      )
    );
    geometry.setAttribute(
      "quaterniondefs",
      new InstancedBufferAttribute(
        new Float32Array(instanceQuaternionsDefs), 4, false
      )
    );

    var material = new RawShaderMaterial({
      transparent: false,
      // depthTest: false,
      side: DoubleSide,
      uniforms: {
        color: new Uniform(new Color(0xb2974d)),
        texturePosition: new Uniform(),
        time: new Uniform()
      },
      blending: AdditiveBlending,
      vertexShader: GoldVertexShader,
      fragmentShader: GoldFragmentShader
    });
    
    this.gold = new InstancedMesh(geometry, material, amount);
    this.gold.layers.set(0);
    this.gold.matrixAutoUpdate = false;
    this.group.add(this.gold);
  }
  initRay() {
    this.raycaster = new Raycaster();
    var geometryRay = new PlaneGeometry( 4, 4, 1, 1 );
    this.meshRay = new Mesh( geometryRay, new MeshBasicMaterial( { color: 0xFFFFFF, visible: false
      // , map: 
      // this.gpuCompute.getCurrentRenderTarget(
      //     this.positionVariable
      //   ).texture
    } ) );
    this.meshRay.rotation.x = - Math.PI / 2;
    this.meshRay.position.y = -0.001;
    this.meshRay.matrixAutoUpdate = false;
    this.meshRay.updateMatrix();
    this.group.add( this.meshRay );
  }
  initComputeRenderer() {
    this.gpuCompute = new GPUComputationRenderer(
      this.width,
      this.width,
      this.renderer
    );
    this.dtPosition = this.gpuCompute.createTexture();

    this.fillPositionTexture(this.dtPosition);

    this.positionVariable = this.gpuCompute.addVariable(
      "texturePosition",
      positionFragmentShader,
      this.dtPosition
    );

    this.gpuCompute.setVariableDependencies(this.positionVariable, [
      this.positionVariable
    ]);

    this.positionUniforms = this.positionVariable.material.uniforms;
    this.positionUniforms["time"] = new Uniform(0);
    this.positionUniforms["positions"] = new Uniform(this.dtPosition.clone());
    this.positionUniforms["predator"] = new Uniform(new Vector2(10000, 10000));

    this.positionVariable.wrapS = RepeatWrapping;
    this.positionVariable.wrapT = RepeatWrapping;
    
    var error = this.gpuCompute.init();
    if (error !== null) {
      window.console.error(error);
    }
    return this.fillPositionTexture
  }
  fillPositionTexture(texture) {
    var theArray = texture.image.data;
    for (var k = 0, kl = theArray.length; k < kl; k += 4) {
      const pos = gaussianRandom();
      var x = pos.x * 0.625; 
      var z = pos.y * 0.625;

      theArray[k + 0] = x;
      theArray[k + 1] = Math.random() * .25 + .0625;
      theArray[k + 2] = z;
      theArray[k + 3] = 1;
    }
    this.dataPos = theArray;
  }
  render(time) {
    this.stars.material.uniforms.time.value = time;
    this.gold.material.uniforms.time.value = time;
    if(settings.idx === 0) {
      if(this.renderer.hasMoved === true) {
        this.raycaster.setFromCamera( settings.mouse, this.camera );

        var intersects = this.raycaster.intersectObject( this.meshRay );

        if ( intersects.length > 0 ) {
          var {point} = intersects[ 0 ];
          this.positionUniforms["predator"].value.set(point.x, point.z - SETTINGS.z);
        } else {
          this.positionUniforms["predator"].value.set(10000, 10000);
        }
        this.renderer.hasMoved = false;
      } else {
        this.positionUniforms["predator"].value.set(10000, 10000);
      }
      this.positionUniforms["time"].value = time;
      this.gpuCompute.compute();
      this.gold.material.uniforms.texturePosition.value = this.gpuCompute.getCurrentRenderTarget(
        this.positionVariable
      ).texture;
    }
  }
  dispose() {
    this.stars.geometry.dispose();
    this.stars.material.dispose();
    this.gold.geometry.dispose();
    this.gold.material.dispose();
    this.scene.remove(this.group);
  }
  resize() {}
}
